.hero-top {
    position: relative;
    height: 100vh;
    min-height: 500px;
    overflow: hidden;
    color: $white;
    &:hover {
        .hero-top__bg {
            transform: scale(1.05); } }
    &__bg {
        @include coverdiv;
        @include cover;
        transition: transform 0.7s; }
    &__inner {
        height: 100%;
        position: relative;
        @include vertical; }
    &__content {
        position: relative;
        display: inline-flex;
        vertical-align: middle;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        @include xs {
            flex-direction: column;
            min-height: 400px;
            align-items: flex-start; } }
    &__play {
        @include vcenter; }
    &__more {
        font-size: 0;
        @include hover {
            .icon {
                animation: bounce 0.4s both; } }
        div {
            display: inline-block;
            vertical-align: middle;
            // margin-right: 89px
            margin-right: 56px;
            // +xl
            //     margin-right: 50px
            // +lg
            //     margin-right: 30px
            @include sm {
                margin-right: 16px; } }
        span {
            display: block;
            font-family: $ermilov;
            // font-size: 50px
            font-size: 34px;
            letter-spacing: -0.02em;
            max-width: 300px;
            line-height: 1;
            &:not(:first-child) {
                margin-top: -4px; }
            @include sm {
                font-size: 24px; } }
        .icon {
            // font-size: 118px
            font-size: 80px;
            display: inline-block;
            vertical-align: middle;
            color: $white;
            margin-top: 5px;
            // +lg
            //     font-size: 90px
            @include sm {
                font-size: 54px; } } }
    &__details {
        position: absolute;
        left: 0;
        bottom: 0;
        display: flex;
        align-items: center;
        height: 25vh;
        text-align: left;
        font-size: 18px;
        @include sm {
            font-size: 21px; }
        span {
            display: block; } } }

.groups {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 50%;
    height: 25vh;
    min-height: 200px;
    font-size: 0;
    @include md {
        position: relative;
        width: 100%; } }

.hero-info {
    position: relative;
    text-align: left;
    h3 {
        font-family: $letterica;
        // font-size: 166px
        font-size: 112px;
        line-height: 1;
        letter-spacing: 0.02em;
        // +xl
        //     font-size: 120px
        // +lg
        //     font-size: 70px
        @include sm {
            font-size: 56px; } }
    &__right {
        position: absolute;
        top: 50%;
        left: 110%;
        transform: translateY(-50%);
        width: 200px;
        text-align: left;
        @include md {
            left: 0;
            top: 120%; }
        @include xs {
            position: static;
            margin-top: 16px; } }
    &__bottom {
        position: absolute;
        top: 100%;
        left: 0;
        min-width: 200px;
        text-align: left;
        @include md {
            top: 140%; }
        @include xs {
            position: relative;
            transform: none; } }
    .info-control {
        width: 100%;
        margin-bottom: 10px;
        @include sm {
            margin-bottom: 4px; } }
    &__top {
        position: absolute;
        left: 0;
        bottom: 100%;
        font-weight: 400;
        // font-size: 32px
        font-size: 21px;
        line-height: 1;
        letter-spacing: 0.04em;
        // +lg
        //     font-size: 24px
        @include sm {
            font-size: 16px; } } }

.group-card {
    display: inline-block;
    width: 50%;
    height: 100%;
    position: relative;
    overflow: hidden;
    @include cover;
    text-align: center;
    &:hover {
        .group-card__bg {
            @include imgScale; } }
    &__bg {
        @include coverdiv;
        @include cover;
        transition: transform 0.7s;
        backface-visibility: hidden; }
    &:after {
        content: '';
        @include coverdiv;
        background-color: rgba($black, 0.5);
        z-index: 1; }
    &__text {
        @include vcenter;
        width: 100%;
        color: $white;
        z-index: 2; } }



