.concerts {
    font-size: 0;
    margin-bottom: 100px;
    @include lg {
        margin-bottom: 50px; }
    @include sm {
        margin-bottom: 10px; } }

.concert {
    display: inline-block;
    vertical-align: top;
    width: 50%;
    color: $black;
    @include sm {
        width: 100%; }
    &__image-wrapper {
        display: block;
        position: relative;
        padding-bottom: 45%;
        overflow: hidden;
        @include gpu;
        &:hover {
            .concert__image {
                @include imgScale; } } }
    &__image {
        @include coverdiv;
        @include cover;
        transition: transform 0.7s;
        backface-visibility: hidden; }
    &__info {
        padding: 28px 50px;
        display: flex;
        flex-wrap: wrap;
        @include md {
            padding: 10px; } }
    &__group {
        @include flex-size(64%);
        position: relative;
        @include lg {
            @include flex-size(100%); }
        .group-title {
            padding: 0 10px 0 0;
            vertical-align: middle; } }
    &__group-online {
        display: inline-block;
        vertical-align: middle;
        margin-left: 10px; }
    &__details {
        @include flex-size(36%);
        line-height: 62px;
        @include lg {
            @include flex-size(100%);
            margin-top: 16px; }
        @include lg {
            line-height: 1; }
        .info-control {
            // margin-bottom: 16px
            &:not(:last-child) {
                margin-right: 18px; } } } }
