.subscr {
    display: inline-flex;
    align-items: center;
    &__image {
        @include circle(70);
        @include cover;
        @include flex-size(70px);
        // +lg
        //     +circle(80)
 }        //     +flex-size(80px)
    &__text {
        @include flex-size(calc(100% - 70px));
        position: relative;
        padding: 24px 0;
        display: flex;
        // flex-direction: column

        align-items: center;
        flex-wrap: wrap;
        margin-left: 16px; }
    &__name {
        display: block;
        // position: relative
        // padding: 20px 0
        // margin-top: auto
        overflow: hidden;
        font-size: 25px;
        line-height: 1;
        font-weight: 700;
        letter-spacing: -0.02em;
        color: $darkBlue;
        transition: color $tr;
        // +xl
        //     font-size: 30px
        // +lg
        //     font-size: 22px
        @include sm {
            font-size: 18px; }
        // &:after
        //     content: ''
        //     position: absolute
        //     bottom: 3px
        //     left: 0
        //     width: 100%
        //     height: 2px
        //     background-color: $darkBlue
        //     transform: translateX(-100%)
        //     transition: transform $tr
        @include hover {
            color: lighten($darkBlue, 20%); }
        //     &:after
        //         transform: translateX(0)
        //      // transform-origin: 100% 0
        .is-online & {
            color: $red; } }
    &__online,
    &__likes {
        position: absolute;
        left: 0; }
    &__online {
        top: 0; }
    &__likes {
        bottom: 0; } }

.subscriptions {
    margin-bottom: 165px;
    @include lg {
        margin-bottom: 100px; }
    @include md {
        margin-bottom: 40px; }
    &__title {
        margin-bottom: 63px;
        @include lg {
           margin-bottom: 50px; } }
    &__main {
        // display: flex
        // flex-wrap: wrap
        margin: 0 -20px;
        .subscr {
            // +flex-size(640px)
            // +flex-size(40%)
            width: 40%;
            padding: 0 20px;
            margin-bottom: 50px;
            @include md {
                margin-bottom: 32px; }
            @include sm {
                @include flex-size(100%); } } } }
