.btn-cta {
    position: relative;
    // padding: 5px 24px 10px 20px
    text-align: center;
    line-height: 1;
    background-color: transparent;
    font-family: $ermilov;
    // font-size: 64px
    color: $white;
    box-shadow : 7px 7px 5px rgba(0, 0, 0, 0.3);
    letter-spacing: -0.012em;
    font-size: 44px;
    padding: 2px 10px 8px;
    &_v1 {
        font-size: 36px;
        padding: 2px 10px 8px; }
    &:hover:after {
        @include fadein; }
    &:before,
    &:after {
        content: '';
        @include coverdiv; }
    &:before {
        background-image: $mainGradient;
        z-index: -2; }
    &:after {
        z-index: -1;
        background-image: $mainGradientReversed;
        @include fadeout($tr); }
    @include xl {
        font-size: 40px;
        padding: 10px 24px 14px; }
    @include lg {
        font-size: 24px;
 } }        // padding: 10px 10px

.gradient-hover {
    @include hover {
        &:after {
            @include fadein; } }
    &:before,
    &:after {
        content: '';
        @include coverdiv; }
    &:before {
        background-image: $mainGradient; }
    &:after {
        z-index: 2;
        background-image: $mainGradientReversed;
        @include fadeout($tr); } }



.btn-load-more {
    font-size: 0;
    background-color: transparent;
    color: $darkBlue;
    & > * {
        display: inline-block;
        vertical-align: middle; }
    &__text {
        margin-left: 10px;
        padding-bottom: 4px;
        font-family: $geo;
        // font-size: 44px
        font-size: 30px;
        font-weight: 700;
        position: relative;
        overflow: hidden;
        &:hover:after {
            animation: line 1s; }
        &:after {
            content: '';
            position: absolute;
            left: 0;
            bottom: 0;
            height: 2px;
            width: 100%;
            background-color: $darkBlue; }
        @include sm {
            font-size: 20px; } }
    .icon {
        // font-size: 48px
        font-size: 31px;
        // +lg
        //     font-size: 36px
        @include sm {
            font-size: 24px; } } }

.btn-submit {
    font-family: $ermilov;
    font-weight: 700;
    font-size: 29px;
    line-height: 1;
    letter-spacing: -0.02em;
    color: $white;
    // background-image: $mainGradient
    background-color: transparent;
    padding: 1px 14px 4px;
    width: 100%;
    text-align: left;
    position: relative;
    @include sm {
        text-align: center; }
    span {
        position: relative;
        z-index: 3; } }

.btn-back {
    font-family: $geo;
    font-weight: 700;
    font-size: 17px;
    line-height: 1.2;
    letter-spacing: -0.02em;
    color: $darkGrey;
    background: none;
    padding: 6px 0;
    width: 100%;
    text-align: left;
    @include sm {
        text-align: center; } }


.btn-play {
    color: transparent;
    background-color: transparent;
    .icon {
        // font-size: 172px
        stroke: $white;
        stroke-width: 16px;
        font-size: 117px;
        // +xl
        //     font-size: 120px
        @include md {
            font-size: 80px; }
        @include sm {
            font-size: 50px; } } }

.btn-edit,
.btn-present,
.btn-notification {
    background: none;
    display: inline-block;
    @include hover {
        .icon {
            color: $red; } }
    .icon {
        color: rgba(#000, 0.4);
        transition: transform $tr; } }



.btn-notification {
    .icon {
        font-size: 23px; } }

.btn-edit {
    .icon {
        font-size: 18px; } }

.btn-present {
    .icon {
        font-size: 25px; } }

.link-cta {
    display: inline-flex;
    vertical-align: middle;
    align-items: center;
    justify-content: center;
    position: relative;
    &_bouncing {
        @include hover {
            .icon {
                animation: bounce 0.4s both; } } }
    &_big {
        @include circle(276);
        background-color: $white;
        @include md {
            @include circle(200); }
        @include hover {
            &:before {
                @include fadein; } }
        &:after,
        &:before {
            @include vcenter;
            @include circle(558);
            content: '';
            @include md {
                @include circle(400); } }
        &:after {
            background-image: $mainGradient;
            z-index: -2;
 }            // background-image: radial-gradient(farthest-side at 50% 50%, transparent 0%, transparent 50%, $purple 50%, $blue 100%)
        &:before {
            opacity: 0;
            visibility: hidden;
            transition: opacity $tr ease-in-out, visibility $tr ease-in-out;
            z-index: -1;
            background-image: linear-gradient(90deg, $blue, $purple); } }
    .main-link {
        position: relative;
        z-index: 2;
        padding: 0 10px; }
    .icon {
        font-size: 80px;
        color: $darkBlue;
        @include md {
            font-size: 60px; }
        @include sm {
            font-size: 30px;
            margin-top: 2px; } } }

@keyframes bounce {
    0% {
        transform: translateX(-20px);
        opacity: 1; }
    24% {
        opacity: 1; }
    40% {
        transform: translateX(-14px);
        animation-timing-function: ease-in; }
    65% {
        transform: translateX(-6px);
        animation-timing-function: ease-in; }
    82% {
        transform: translateX(-3px);
        animation-timing-function: ease-in; }
    93% {
        transform: translateX(-2px);
        animation-timing-function: ease-in; }
    25%,
    55%,
    75%,
    87% {
        transform: translateX(0px);
        animation-timing-function: ease-out; }
    100% {
        transform: translateX(0px);
        animation-timing-function: ease-out;
        opacity: 1; } }

.main-link {
    color: $darkBlue;
    font-size: 32px;
    font-family: $ermilov;
    letter-spacing: -0.06em;
    // +xl
    //     font-size: 40px
    // +lg
    //     font-size: 30px
    @include sm {
        font-size: 20px; } }

@keyframes line {
    0% {
        transform: translateX(0); }
    50% {
        transform: translateX(100%); }
    52% {
        transform: translateX(-100%); }
    100% {
        transform: translateX(0); } }
