.ticket {
	height: 446px;
	width: 215px;
	color: $white;
	// +lg
	// 	height: 528px
	// 	width: 255px
	@include hover {
		.btn-notification,
		.btn-edit,
		.btn-present {
			@include fadein;
			transform: translateX(0); }
		.ticket__bottom {
			&:before {
				@include fadein; } } }
	&__top {
		position: relative;
		height: 65.9%; }
	&__image {
		@include cover;
		@include coverdiv;
		clip-path: url(#ticket-top);
		// +lg
 }		// 	clip-path: url(#ticket-top-lg)
	&__actions {
		position: absolute;
		top: 0;
		left: 100%;
		padding-left: 12px;
		padding-top: 12px;
		text-align: center;
		@include lg {
			padding-left: 6px; }
		& > * {
			display: inline-block;
			opacity: 0;
			visibility: hidden;
			transition: opacity 0.35s, visibility 0.35s, transform 0.45s;
			transform: translateX(-60%);
			&:not(:last-child) {
				margin-bottom: 12px; } }
		.btn-edit {
			transition-delay: 0.15s; }
		.btn-present {
			transition-delay: 0.3s; } }
	&__bottom {
		position: relative;
		clip-path: url(#ticket-bottom);
		height: 34.1%;
		box-shadow : 7px 7px 5px rgba(#000, 0.65);
		position: relative;
		&:after,
		&:before {
			@include coverdiv;
			content: '';
			z-index: -2; }
		&:before {
			@include fadeout($tr);
			z-index: -1; }
		// +lg
		// 	clip-path: url(#ticket-bottom-lg)
		&_blue {
			&:after {
				background-image: $blueGradient; }
			&:before {
				background-image: $blueGradientReversed; } }
		&_orange {
			&:after {
				background-image: $orangeGradient; }
			&:before {
				background-image: $orangeGradientReversed; } }
		&_red {
			&:after {
				background-image: $redGradient; }
			&:before {
				background-image: $redGradientReversed; } } }

	&__text {
		// position: absolute
		// bottom: 17px
		// left: 30px
		padding: 0 10px 5px 20px;
		text-align: left;
		font-weight: 700;
		@include lg {
			padding: 0 10px 10px; } }
	&__logo-wrapper {
		height: 100px;
		padding: 0 10px;
		@include vertical; }
	&__logo {
		vertical-align: middle;
		max-width: 100%; }
	&__place {
		font-family: $ermilov;
		font-size: 13px;
		letter-spacing: -0.04em;
		line-height: 1; }
	&__time {
		font-size: 12px;
		letter-spacing: -0.025em; } }


.tickets {
		padding-top: 218px;
		margin-bottom: 86px;
		@include lg {
			padding-top: 150px;
			margin-bottom: 50px; }
		&__title {
				margin-bottom: 40px; }
		&__main,
		&__secondary {
				display: flex;
				flex-wrap: wrap;
				// padding-right: 200px
				margin-top: 30px;
				position: relative;
				@include lg {
					padding-right: 140px; }
				@include sm {
					padding-right: 0;
					flex-direction: column;
 }					// align-items: center
				.ticket {
						position: relative;
						z-index: 3;
						margin-bottom: 32px;
						&:not(:last-child) {
								margin-right: 10%;
								// +xl
								// 		margin-right: 70px
								// +lg
								// 		margin-right: 50px
								@include sm {
										margin-right: 0; } } } }

		&__secondary {
				max-height: 0;
				overflow: hidden;
				transition: max-height 1s;
				&.is-active {
						max-height: 1000px; } }
		&__cta {
				position: absolute;
				right: -172px;
				top: 223px;
				transform: translateY(-56%);
				@include xl {
					right: -70px; }
				@include lg {
					right: -15px;
					top: 254px; }
				@include xs {
					position: static;
					margin-top: 100px; } } }
