.footer {
    position: relative;
    padding: 60px 0 80px;
    background-color: $lightGrey;
    border-radius: 10px;
    &__inner {
        position: relative; }
    @include md {
        padding: 40px 0; }
    @include sm {
        padding-top: 90px; }
    &__logo {
        @include vcenter;
        @include md {
            top: 40px;
            left: 50%;
            transform: translateX(-50%); }
        @include sm {
            top: -60px;
            left: 8.333%;
            transform: none; } }
    &__content {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        @include md {
            align-items: flex-start; }
        @include sm {
            text-align: left; } }
    &__left {
        @include flex-size(69%);
        @include xs {
            @include flex-size(100%);
            margin-bottom: 20px; } }
    &__right {
        @include flex-size(31%);
        @include xs {
            @include flex-size(100%); } }
    &__row {
        &:not(:last-child) {
            margin-bottom: 30px;
            @include lg {
                margin-bottom: 0; } }
        .link:first-child {
            margin-right: 40px;
            @include md {
                margin-right: 0; } }
        @include lg {
            .outer-link {
                display: block; } } }
    .links {
        margin-bottom: 13px; } }

.link {
    position: relative;
    display: inline-block;
    padding-bottom: 3px;
    font-weight: 700;
    font-size: 21px;
    letter-spacing: -0.03em;
    line-height: 1;
    margin-bottom: 5px;
    transition: color 0.2s;
    overflow: hidden;
    &:hover:after {
        transform: translateX(0); }
    &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        height: 1px;
        width: 100%;
        transform: translateX(-100%);
        background-color: $black;
        transition: transform $tr; } }

.copyright {
    font-size: 21px;
    color: $grey; }

#clip-svg {
    position: absolute;
    bottom: 0;
    left: 0; }
