.chanels-section {
    margin: 100px auto 10px;
    @include lg {
        margin: 40px auto 0; }
    &__title {
        margin-bottom: 50px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        .title-big {
            padding-right: 50px; } }
    &__row {
        display: flex;
        flex-wrap: wrap;
        .card-online {
            min-width: 25%;
            @include md {
                @include flex-size(100%);
                margin-bottom: 24px; }
            @include sm {
                margin-bottom: 12px; } }
        .chanel {
            flex-grow: 1; } }
    .chanel {
        &:not(:last-child) {
            margin-bottom: 32px; }
        @include md {
            margin-bottom: 24px; }
        @include sm {
            margin-bottom: 12px; } }
    &__load-more {
        padding: 5px 0 40px;
        text-align: center;
        @include md {
            padding: 5px 0 30px; } } }


.tags {
    margin-left: auto;
    // padding-left: 50px
    @include md {
        width: 100%;
        margin: 10px 0 0; } }


.tag {
    display: inline-block;
    padding: 2px 10px 10px 10px;
    font-family: $ermilov;
    font-size: 42px;
    line-height: 1;
    letter-spacing: -0.06em;
    position: relative;
    transition: color $tr;
    @include lg {
        font-size: 32px;
        padding: 1px 5px 5px; }
    @include sm {
        font-size: 24px; }
    &:before,
    &:after {
        content: '';
        @include coverdiv; }
    &:before {
        background-image: $mainGradient;
        z-index: 1;
        @include fadeout($tr); }
    &:after {
        z-index: 1;
        background-image: linear-gradient(90deg, $blue, $purple);
        @include fadeout($tr); }
    &:hover {
        color: $white;
        &:before {
            @include fadein; } }

    &.is-active {
        color: $white;
        &:before, {
            opacity: 1;
            visibility: visible; }
        &:hover:after {
            @include fadein; } }
    span {
        position: relative;
        z-index: 3; } }


.tag-small {
    line-height: 1;
    display: inline-block;
    vertical-align: top;
    font-size: 14px;
    letter-spacing: -0.02em;
    position: relative;
    // +lg
    //     font-size: 20px
    // +sm
    //     font-size: 16px
    &:hover:after {
        @include fadein; }
    &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -4px;
        height: 2px;
        width: 100%;
        background-color: $white;
        @include fadeout($tr); } }


.chanel {
    position: relative;
    height: 210px;
    padding: 32px 0 18px;
    @include cover;
    color: $white;
    @include md {
        padding: 10px 18px; }
    &:after {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 40%;
        content: '';
        background-image: linear-gradient(90deg, rgba($black, 1) 0%, transparent 68.65%); }
    &__inner {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        // max-width: 1800px
        height: 100%;
        padding: 0 70px;
        margin: 0 auto;
        z-index: 5;
        @include lg {
            padding: 0 20px; }
        @include sm {
            flex-direction: column;
            align-items: flex-start; } }

    &__info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
        @include sm {
            margin-bottom: 16px; } }
    &__logo {
        @include circle(70);
        @include cover; }
    &__group {
        font-size: 12px;
        letter-spacing: -0.02em;
        line-height: 1.2;
        font-weight: 700; }
    &__name {
        font-size: 25px;
        line-height: 1;
        letter-spacing: -0.02em;
        @include sm {
            font-size: 18px; } }
    &__likes {
        margin-top: auto;
        .icon-heart {
            color: red; } }
    &__tags {
        line-height: 22px;
        & > * {
            margin-right: 3px; }
        // float: right
 } }        // display: flex

