.header {
    position: absolute;
    top: 44px;
    left: 0;
    width: 100%;
    z-index: 15;
    // &.is-active
    //     background-color: $white
    &__inner {
        margin-left: calc(50% - 550px);
        margin-right: calc(50% - 800px);
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include lg {
            padding: 0 8.333%;
            margin: 0 auto; }
        &_center {
            max-width: 1100px;
            margin: 0 auto;
            padding: 0 10px;
            @include lg {
                padding: 0 8.333%; } }
        // +xl
        //     max-width: 1100px
        //     margin: 0 auto
 }        //     padding: 0 10px
    &__logo {
        .logo__text {
            @include sm {
                display: none; } } }
    &__search {
        @include vcenter;
        width: 215px;
        z-index: 10;
        @include lg {
            position: relative;
            top: 0;
            left: 0;
            flex-grow: 1;
            padding: 0 50px;
            transform: none; }
        @include md {
            // order: 2
            // width: 34px
            padding: 0 10px;
            width: auto;
            flex-grow: 0; }
        @include sm {
            width: 30px;
            margin-left: auto;
            padding-right: 40px;
            .is-active & {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 60px;
                width: calc(100% - 70px); } } }
    &__nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include flex-size(612px);
        transition: 0.4s opacity, 0.4s visibility;
        .header__inner_center & {
            justify-content: flex-end; }
        @include xl {
            @include flex-size(auto); }
        .is-active & {
            @include sm {
                opacity: 0;
                visibility: hidden; } }
