        //     .is-active &
        //         transform: translateX(0)
 }        //         z-index: -1
    // &__toggle
    //     display: none
    //     +md
 }    //         display: block


.logo {
    display: flex;
    align-items: center;
    color: $white;
    @include gpu;
    &:hover {
        .logo__rotated-img {
            @include fadein; } }
    &_dark {
        color: $black; }
    &__image {
        // width: 118px
        // height: 118px
        height: 80px;
        width: 80px;
        transition: transform $tr;
        position: relative;
        @include md {
            width: 50px;
            height: 50px; }
        img {
            max-width: 100%;
            position: absolute;
            top: 0;
            left: 0; } }
    &__rotated-img {
        transform: scale(-1);
        z-index: 2;
        @include fadeout($tr); }
    &__text {
        position: relative;
        margin-left: 11px; }
    &__name {
        font-family: $ermilov;
        // font-size: 48px
        font-size: 32px;
        letter-spacing: -0.057em;
        // +xl
        //     font-size: 40px
        // +md
 }        //     font-size: 30px
    &__descr {
        position: absolute;
        left: 0;
        top: 46px;
        margin-right: -20px;
        font-weight: 300;
        font-size: 24px;
        font-size: 16px;
        line-height: 1.1;
        // +xl
        //     font-size: 18px
        //     top: 50px
        @include md {
            display: none; } } }

.nav {
    color: $white;
    font-size: 0;
    .header_dark & {
        color: $black;
        .hover-invis {
            fill: rgba($black, 0.8); }
        .hover-vis {
            fill: rgba($black, 0.8); } }
    .header__inner_center & {
        margin-right: 70px;
        margin-left: auto;
        @include sm {
            margin-right: 10px; } }
    &__list {
        display: flex;
        align-items: center;
        @include xl {
            margin-right: 30px; }
        @include md {
            margin-right: 12px; } }
    &__item {
        &:not(:last-child) {
            margin-right: 20px;
            @include sm {
                margin-right: 6px; } } }
    &__link {
        display: inline-block;
        vertical-align: middle;
        &:hover,
        &.is-active {
            .hover-invis {
                opacity: 0; }
            .hover-vis {
                opacity: 1; } }
        svg {
            // height: 76px
            // width: 76px
            height: 52px;
            width: 52px;
            // +xl
            //     height: 50px
            //     width: 50px
            @include md {
                height: 40px;
                width: 40px; }
            @include sm {
                height: 30px;
                width: 30px; } }
        .hover-invis {
            opacity: 1;
            fill: $white;
            transition: opacity 0.3s; }
        .hover-vis {
            fill: $white;
            opacity: 0;
            transition: opacity 0.3s; } } }

.user-info {
    margin-left: auto;
    color: $white;
    @include md {
        margin-right: auto; }
    &__list {
        display: flex;
        align-items: center;
        justify-content: center; }
    &__name {
        // font-size: 24px
        font-size: 13px;
        line-height: 1.1;
        font-weight: 300;
        @include md {
            display: none; } }
    &__avatar {
        display: block;
        position: relative;
        margin-left: 32px;
        // height: 88px
        // width: 88px
        width: 60px;
        height: 60px;
        &_colored {
            &:after {
                content: '';
                height: calc(100% + 12px);
                width: calc(100% + 12px);
                border-radius: 50%;
                @include vcenter;
                z-index: -1;
                background-image: $mainGradient;
                // +xl
                //     height: calc(100% + 8px)
 } }                //     width: calc(100% + 8px)
        img {
            max-width: 100%; }
        @include xl {
            height: 60px;
            width: 60px;
            margin-left: 24px; }
        @include md {
            height: 50px;
            width: 50px;
            margin-left: 12px; } } }

.avatar {
    border-radius: 50%; }

.search {
    position: relative;
    width: 100%;
    &__input {
        width: 100%;
        background-color: transparent;
        border: 3px solid rgba($white, 0.5);
        height: 34px;
        padding: 10px 49px 10px 26px;
        color: $white;
        font-size: 18px;
        border-radius: 17px;
        .header_dark & {
            border: 3px solid rgba($black, 0.5);
            color: $black; }
        &:focus {
            border-color: $white;
            .header_dark & {
                border-color: $black; } }
        @include sm {
            @include fadeout(0.4s);
            padding-right: 35px; }
        .is-active & {
            @include sm {
                @include fadein; } } }

    &__button {
        position: absolute;
        top: 50%;
        right: 14px;
        transform: translateY(-50%);
        background-color: transparent;
        font-size: 0;
        @include sm {
            display: none;
            right: 50%;
            transform: translate(50%, -50%); }
        .is-active & {
            @include sm {
                right: 22px;
                display: block; } }
        &_mob {
            display: none;
            @include sm {
                display: block; }
            .is-active & {
                @include sm {
                    opacity: 0; } } }
        .icon {
            color: $white;
            font-size: 13px;
            .header_dark & {
                color: $black; }
            .is-active & {
                @include sm {
                    font-size: 13px; } } } }

    &__close {
        display: none;
        background-color: transparent;
        position: absolute;
        right: -30px;
        top: -10px;
        .is-active & {
            @include sm {
                display: block; } }
        .icon {
            font-size: 16px;
            color: $white; } } }

.toggle-button {
    position: relative;
    z-index: 20;
    width: 40px;
    height: 30px;
    display: none;
    @include md {
        display: block; }
    span {
        display: block;
        width: 100%;
        height: 3px;
        width: 30px;
        background: $white;
        margin: 6px auto;
        transition: all 0.3s;
        backface-visibility: hidden; } }

.toggle-button.on {
    span:nth-child(1) {
        transform: rotate(45deg) translate(4px, 10px); }
    span:nth-child(2) {
        opacity: 0; }
    span:nth-child(3) {
        transform: rotate(-45deg) translate(3px, -9px); } }

.login {
    font-size: 21px;
    letter-spacing: -0.02em;
    color: $white;
    position: relative;
    overflow: hidden;
    display: inline-block;
    @include lg {
        font-size: 24px; }
    @include sm {
        font-size: 20px; }
    &:hover:after {
        transform: translateX(0); }
    &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        height: 1px;
        width: 100%;
        transform: translateX(-100%);
        background-color: $white;
        transition: transform $tr; } }
