.best-section {
    &__title {
        margin: 150px auto 42px;
        @include lg {
            margin: 40px; } } }

.new-section {
    margin-top: 82px;
    @include lg {
        margin-top: 40px; }
    &__title {
        margin-bottom: 50px;
        @include lg {
            margin-bottom: 40px; } } }

.likes {
    padding-bottom: 120px;
    @include lg {
        left: 100px; }
    @include md {
        padding-bottom: 40px; }
    &__title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-bottom: 85px;
        @include lg {
            margin-bottom: 30px; }
        .link-cta {
            margin-top: -20px;
            @include lg {
                margin-top: 6px; }
            @include xs {
                margin-left: -10px;
                width: 100%;
                justify-content: flex-start; } } } }


