.buy {
    padding: 80px 0 55px;
    @include lg {
        padding: 40px 0; }
    &__inner {
        display: flex;
        flex-wrap: wrap;
 }        // justify-content: space-between
    &__cta {
        padding-top: 24px;
        &_v1 {
            padding-top: 14px; } }
    &__cta-btn {
        width: 100%; } }

.cta {
    &_white {
        color: $white; }
    &__top {
        display: flex;
        align-items: center;
        @include md {
            flex-direction: column;
            align-items: flex-start; } }
    &__price {
        margin-left: 24px;
        line-height: 1;
        font-family: $ermilov;
        font-size: 44px;
        letter-spacing: -0.02em;

        // +lg
        //     font-size: 48px
        @include md {
            font-size: 30px;
            margin-left: 0;
            margin-top: 18px; } }

    &__text {
        margin-top: 20px;
        line-height: 1;
        font-size: 20px;
        // &_v1
        //     font-size: 16px
        // +xl
        //     font-size: 24px
        //     margin-top: 24px
        @include md {
            font-size: 18px; } } }


