.hero-middle {
    height: 56vh;
    min-height: 1080px;
    background-image: url(../img/hero-middle.png), $mainGradient;
    background-position: 0 100%, 0 0;
    background-repeat: no-repeat;
    position: relative;
    background-size: contain;
    @include lg {
        min-height: 800px; }
    @include md {
        min-height: 700px; }
    @include sm {
        min-height: 500px; }
    &__close {
        display: inline-block;
        background-color: transparent;
        position: absolute;
        top: 46px;
        right: 30px;
        font-size: 34px;
        @include lg {
            font-size: 20px;
            top: 20px;
            right: 10px; } }
    &__text {
        display: flex;
        flex-wrap: wrap;
        padding-top: 70px;
        @include lg {
            padding-top: 40px; } }
    &__slogan-middle {
        font-family: $ermilov;
        font-size: 44px;
        letter-spacing: -0.05em;
        line-height: 1;
        max-width: 70%;
        @include md {
            font-size: 36px; }
        @include sm {
            font-size: 24px; } }
    &__slogan-big {
        font-family: $ermilov;
        line-height: 1;

        font-size: 74px;
        color: $white;
        text-align: right;
        letter-spacing: -0.04em;
        margin-top: 14px;
        @include lg {
            font-size: 55px; }
        @include md {
            font-size: 40px;
            text-align: left;
            margin-bottom: 16px; }
        @include sm {
            font-size: 36px; } }
    p {
        margin-top: 26px;
        max-width: 80%;
        font-size: 24px;
        // letter-spacing: -0.03em
        line-height: 1.07;
        color: $white;
        @include md {
            font-size: 18px; } }
    .col-31 {
        @include md {
            order: 1; } }
    .col-69 {
        @include md {
            order: 2; } } }
