.translation {
    position: relative;
    height: 100vh;
    min-height: 900px;
    background: $mainGradient;
    &.is-modal-open {
        &:after {
            display: block; } }

    &:after {
        @include coverdiv;
        content: '';
        background-color: rgba($black, 0.4);
        pointer-events: none;
        display: none; } }


