body {
    background: #FFF;
    line-height: 1.4;
    font-family: $geo;
    font-size: 16px;
    color: $black;
    min-width: 320px;
    font-weight: 400; }

.title {
    font-family: $letterica;
    // font-size: 108px
    font-size: 73px;
    line-height: 1;
    letter-spacing: 0.02em;
    display: inline-block;
    vertical-align: middle;
    @include md {
        font-size: 50px; }
    @include sm {
        font-size: 36px; } }

.title-big {
    font-family: $letterica;
    font-size: 94px;
    letter-spacing: 0.02em;
    line-height: 1;
    text-transform: uppercase;
    display: inline-block;
    vertical-align: middle;
    // +xl
    //     font-size: 90px
    @include md {
        font-size: 50px; }
    @include sm {
        font-size: 36px; } }

.subtitle {
    display: inline-block;
    vertical-align: middle;
    max-width: 300px;
    margin-top: -10px;
    margin-left: 24px;
    font-weight: 400;
    // font-size: 37px
    font-size: 25px;
    line-height: 1.1;
    letter-spacing: -0.02em;
    // +xl
    //     font-size: 30px
    // +lg
    //     font-size: 22px
    @include sm {
        font-size: 18px;
        display: block;
        margin-left: 0;
        margin-top: 0; } }


.group-title {
    padding: 0 10px;
    font-family: $letterica;
    font-size: 62px;
    line-height: 1;
    letter-spacing: 0.02em;
    @include md {
        font-size: 40px; }
    @include sm {
        font-size: 36px; } }

.numbers-title {
    position: relative;
    display: inline-block;
    &__number {
        display: inline-block;
        vertical-align: middle;
        position: absolute;
        top: 3px;
        left: 103%;
        @include circle(32);
        background-image: $mainGradient;
        text-align: center;
        // +xl
        //     +circle(40)
        // +lg
        //     +circle(32)
        //     top: -5px
        @include md {
            position: static; }
        span {
            margin-top: 2px;
            display: inline-block;
            vertical-align: top;
            line-height: 1;
            font-family: $ermilov;
            font-size: 27px;
            color: $white; } } }




.out {
    overflow: hidden; }
